import React, { useState, useEffect } from 'react';
import ProfileP from '../screens/ProfileP';
import {ossoApi} from '../redux/api'
import * as formData from 'form-data';
import {connect} from 'react-redux'
function Profile(props) {
    const {user}=props
    const [userPic, setUserPic] = useState(null);
    const [picture,setPicture]=useState(null)
    const [name,setName]=useState(null)
    const [email,setEmail]=useState(null);
    const [phone,setPhone]=useState(null)
    const [address,setAddress]=useState(null)
    const [isChange,setIsChange]=useState(true)
    const [isPic,setIsPic]=useState(false)
    const [isProfile,setIsProfile]=useState(false)
    if(user && isChange)
    {
        console.log("user btao",user)
        user.fullname ? setName(user.fullname) : setName('Enter name')
        user.email ? setEmail(user.email) : setEmail('Enter Email')
        user.phone ? setPhone(user.phone) : setPhone('Enter Phone Number')
        user.address ? setAddress(user.address) : setAddress('Enter Address')
        user.imageUrl ? setPicture(user.imageUrl) :setPicture(null)
        setIsChange(false)
    }
    const handleUpdate=()=>{
        if(userPic !=user.profiles)
        {
            const dataBody = new formData();
            dataBody.append('profiles', userPic);
            ossoApi.post(`/user/${user._id}/profile-image`, dataBody).then(res => {
                console.log("photo", res)
                if(res.data && res.data.data)
                {
                    setIsPic(true)
                }
                else{
                    alert('Try Again!')
                }
            }).catch(err=>{
                console.log("photo error",err)
            })
        }
       
       let data={
           phone,
           fullname:name,
           address
       }
       let dataInput=JSON.stringify(data)
        ossoApi.put(`user/${user._id}`,dataInput,{
            headers: {
                'content-type': 'application/json'
            }
        }).then(res=>{
            console.log("info",res)
            if(res.data && res.data.data)
            {
                alert('Profile Updated Sucessfully')
                setIsProfile(true)
            }
            else
            {
                alert('Try Again!')
            }
        }).catch(err=>{
            console.log(err)
            alert('try Again')
        })
    }
    if(isPic && isProfile)
    {
            props.history.push('/');
    }
    return (
        <ProfileP
            setUserPic={setUserPic}
            userPic={userPic}
            picture={picture}
            name={name}
            setName={setName}
            email={email}
            phone={phone}
            setPhone={setPhone}
            address={address}
            setAddress={setAddress}
            handleUpdate={handleUpdate}
        />
    );
}
const mapStateToProps=(state)=>{
    return{
        user:state.auth.user,
    }
}
export default connect(mapStateToProps)(Profile);
