import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import {IconContext} from "react-icons";
import {FaTrash, FaPen} from 'react-icons/fa';

const ProfileP = (props) => (    
        <div className="login-outer">
            <div className="login-container">
                <h3 className="form-heading">Settings</h3>
                <div className="form">
                    <div className="form-element">
                        <div className="upload-img">
                            <Link class="edit-img">
                                <IconContext.Provider value={{ color: "white", size: "14" }}>
                                    <span><FaPen /></span>
                                </IconContext.Provider>
                            </Link>
                            <div className="img-border">                                
                                <input type="file" className="upload-photo" accept="image/*" onChange={(e) => e.target.files[0] && props.setUserPic(e.target.files[0])} />
                                <div id="priview_image" className="">
                                    {(props.picture || props.userPic) &&
                                    <div class="imgWrap">
                                        <img id="priview_image_src" src={props.userPic ? URL.createObjectURL(props.userPic):props.picture} /> 
                                        <Link onClick={() => props.setUserPic(null)} class="close-img">
                                            <IconContext.Provider value={{ color: "white", size: "14" }}>
                                            <span><FaTrash /></span>
                                            </IconContext.Provider>
                                        </Link>
                                    </div>
                                    }
                                </div>
                            </div>
                        </div>
                        <div className="error"></div>
                    </div>
                    <div className="form-element">
                    <input type="text" value={props.name} onChange={(e) => { props.setName(e.target.value) }}/>
                        <div className="errro-msg"></div>
                    </div>
                    <div className="form-element">
                    <input type="tel" value={props.phone} onChange={(e) => { props.setPhone(e.target.value) }}/>
                        <div className="errro-msg"></div>
                    </div>
                    <div className="form-element">
                    <input type="email" value={props.email} disabled />
                        <div className="errro-msg"></div>
                    </div>
                    <div className="form-element">
                    <input type="text" value={props.address} onChange={(e) => { props.setAddress(e.target.value) }}/>
                        <div className="errro-msg"></div>
                    </div>
                </div>
                {/* <div className="row-section">
                    <div className="remember-me">
                        <label className="checkbox">Remember me
                                <input type="checkbox" /><i className="input-helper"></i>
                        </label>
                    </div>
                    <Link href="javascript:;">Forgot Password?</Link>
                </div> */}
                <div className="submit-wrap">
                    <Link onClick={props.handleUpdate}>Update</Link>
                </div>               
            </div>
        </div>
    );

export default ProfileP;
